html {
  scroll-behavior: smooth;
}

.App {
  display: flex;
  flex-direction: column;
  background-color: white;
  /*overflow: hidden;*/
  margin: 0 !important;
  padding: 0 !important;
}

.background {
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 40%, rgba(159, 150, 178, 0.18) 100%);
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 1;

  pointer-events: none; /* allow click thru */
}

/* Creation of grain-y effect in the background */
.background::after {
  content: "";
  background-image: url(data:image/png;base64,);
  /*background-image: url(data:image/png;base64,);*/

  height: 200vh;
  width: 200vw;
  position: fixed;
  top: -50%;
  left: -50%;
  z-index: 1;

  animation:grain 5s steps(6) infinite;
  pointer-events: none; /* allow click thru */
}

@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}